<template>
  <v-container fluid fill-height class="d-flex flex-column">
    <div style="width: 100%;" class="baseColor">
        <v-toolbar
            class="mb-2"
            color="secondary"
            dark
            flat
            >
                <v-toolbar-title>{{tipoOpcion}}</v-toolbar-title>
        </v-toolbar>


        <div class="mt-10 mr-4 ml-4 mb-10">
            <v-col cols="6">
                <v-row>
                    <v-col cols="2">
                        <p  class="mb-0 mt-2">Estado: </p>
                    </v-col>
                    <v-col
                        cols="7"
                        >
                            <v-autocomplete
                            :items="status" 
                            v-model="statusSelected.key"
                            item-value="key" 
                            item-text="name"
                            outlined
                            rounded
                            dense
                            hide-details
                        ></v-autocomplete>
                    </v-col>
                    <v-col
                        class="mt-0 pb-0 mb-0"
                        md="2">
                        <Boton :btnData="btnBuscar" :click="clickBuscar"/>
                    </v-col>
                </v-row>
            </v-col>
            <v-col
                class="mt-0 pa-0 mb-9"
                md="1">
                <Boton :btnData="btnAgregar" :click="clickAgregar"/>
            </v-col>


            <ChargeData 
                :loading = "loading"
            >
                    <template slot="msgLoading">
                            <p v-show="loading">Cargando los datos...</p>
                    </template>
            </ChargeData>
            <Notification
                v-if="activeNotification"
                v-model="activeNotification"
                :isError="errorDetected"
                :messages = "messagesNotification"
            />
            <TableEditAndDelete 
                :disableOptionIn="obtainItemDisabled"
                typeRegister = "profile"
                v-if="!loading"
                :items="profiles" 
                :headers="headers" 
                :editedItem="moduleItems"
                :funcEditItem = "funcEditItem"
                :enableItem = "enableItem"
                :deleteItem = "disableItem"
            />
            <v-dialog
                v-if= "dialogEdit"
                v-model="dialogEdit"
                persistent
                max-width="70%"
                >
                <v-card>
                    <v-toolbar dense color="primary">
                        <v-toolbar-title class="white--text">
                            <span class="text-h5">Editar Perfil</span>
                        </v-toolbar-title>
                        <v-spacer></v-spacer>
                    </v-toolbar>
                    <v-container v-if = "showAlertInfo">
                        <v-alert
                            type="info"
                        > {{textShowAlertInfo}} </v-alert>
                    </v-container>
                    <v-container>
                        <ChargeData
                            :loading = "loadingSaveData"

                        >
                            <template slot="msgLoading">
                                <p v-show="loadingSaveData">Guardando los datos...</p>
                            </template>
                        </ChargeData>
                    </v-container>
                    <v-card-text v-if="!loadingSaveData">
                        <v-container>
                            <v-row>
                            <v-col cols="12"
                                sm="6"
                                md="4" 
                                v-for="(item, index) in moduleItems" :key="index">
                                <CardProfile :title="item.name">
                                    <template slot="module">
                                        <v-checkbox
                                            v-for="(opcion, index) in item.options" :key="index"
                                            :label="opcion.name"
                                            :value="{idModule: opcion.idModule, idOption: opcion.idOption }"
                                            v-model="selected"
                                        >
                                        </v-checkbox>
                                    </template>
                                    
                                </CardProfile>
                            </v-col>
                            </v-row>
                        </v-container>
                    </v-card-text>

                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn
                            color="blue darken-1"
                            text
                            @click="closeDialog"
                        >
                            Cancelar
                        </v-btn>
                        <v-btn
                            color="blue darken-1"
                            text
                            @click="saveProfile"
                        >
                            Guardar
                        </v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>

            <DialogAcceptCancel 
                :activeDialog = "dialogEnableProfile"
            >
                <template slot="titleCard">
                    <span class="text-h5">Habilitar Perfil</span>
                </template>
                <template slot="mainDialog">
                    <v-container>
                        <v-alert
                            type="success"
                        >
                            Está seguro/a que desea habilitar el perfil: {{editProfileSelected.name}}
                        </v-alert>
                    </v-container>
                </template>
                <template slot="cancel">
                    <v-btn
                        color="blue darken-1"
                        text
                        @click="closeDialog"
                    >
                        Cancelar
                    </v-btn>
                </template>
                <template slot="accept">
                    <v-btn
                        color="blue darken-1"
                        text
                        @click="enableProfile"
                    >
                        Aceptar
                    </v-btn>
                </template>
            </DialogAcceptCancel>

            <DialogAcceptCancel 
                :activeDialog = "dialogAddProfile"
            >
                <template slot="titleCard">
                    <span class="text-h5">Agregar Perfil</span>
                </template>
                <template slot="mainDialog">
                    <v-container v-if = "showAlertInfo">
                        <v-alert
                            type="info"
                        > Debe ingresar los datos correctamente. Recuerde no colocar tipos numéricos en campos de texto </v-alert>
                    </v-container>
                    <v-container>
                       <v-row
                       > 
                            <v-col
                                cols="12"
                                md="6"
                            >
                                <v-row>
                                    <v-col
                                        class="pr-0"
                                        cols="2"
                                        md="2"
                                    >
                                        <p class="text-right ma-0 mt-2">Nombre:</p>
                                    </v-col>
                                    <v-col
                                        class="pr-0"
                                        cols="9"
                                        md="10"
                                    >
                                        <TextFieldSimpleData 
                                            v-if= "dialogAddProfile"
                                            v-model="nameProfile"
                                            label= "Nombre del perfil"
                                        />
                                    </v-col>
                                </v-row>
                            </v-col>
                            <v-col
                                cols="12"
                                md="6"
                            >
                                <v-row>
                                    <v-col
                                        class="pr-0"
                                        cols="2"
                                        md="3"
                                    >
                                        <p class="text-right ma-0 mt-2">Descripción:</p>
                                    </v-col>
                                    <v-col
                                        class="pr-0"
                                        cols="9"
                                        md="8"
                                    >
                                        <TextFieldSimpleData 
                                            v-if= "dialogAddProfile"
                                            v-model="descriptionProfile"
                                            label= "Descripción del perfil"
                                        />
                                    </v-col>
                                </v-row>
                            </v-col>
                        </v-row>
                    </v-container>
                        
                </template>
                <template slot="cancel">
                    <v-btn
                        color="blue darken-1"
                        text
                        @click="closeDialog"
                    >
                        Cancelar
                    </v-btn>
                </template>
                <template slot="accept">
                    <v-btn
                        color="blue darken-1"
                        text
                        @click="addProfile"
                    >
                        Guardar
                    </v-btn>
                </template>
            </DialogAcceptCancel>

            <DialogAcceptCancel 
                :activeDialog = "dialogDisableProfile"
            >
                <template slot="titleCard">
                    <span class="text-h5">Deshabilitar Perfil</span>
                </template>
                <template slot="mainDialog">
                    <v-container>
                        <v-alert
                            type="warning"
                        >
                            Está seguro/a que desea deshabilitar el perfil: {{editProfileSelected.name}}
                        </v-alert>
                    </v-container>
                </template>
                <template slot="cancel">
                    <v-btn
                        color="blue darken-1"
                        text
                        @click="closeDialog"
                    >
                        Cancelar
                    </v-btn>
                </template>
                <template slot="accept">
                    <v-btn
                        color="blue darken-1"
                        text
                        @click="disableProfile"
                    >
                        Aceptar
                    </v-btn>
                </template>
            </DialogAcceptCancel>
            

        </div>
    </div>
  </v-container>
</template>

<script>
import Boton from "../../components/Boton.vue";
import TableEditAndDelete from "../../components/TableEditAndDelete.vue";
import ChargeData from "../../components/ChargeData.vue";
import DialogAcceptCancel from "../../components/DialogAcceptCancel.vue";
import TextFieldSimpleData from "../../components/TextFieldSimpleData.vue";
import CardProfile from "../../components/CardProfile.vue";
import Notification from "../../components/Notification.vue";
import { findProfiles, findProfileOptions, enableProfile, disableProfile, updateProfileInOption, saveProfile } from '../../services/DataServices';
import { btnAgregar, btnBuscar } from "../../types/btnDesign.js";
import { setterProfilesInOptionsActives, createProfilesInOptionsUpdates, setterErrorData } from '../../helpers/setterData';
import { findLocalStorage } from "../../helpers/handleLocalStorage.js";

export default {
    name: "RegistrarUser",
    created: function(){
        this.getProfiles();
    },
    data: () => ({
        tipoOpcion: "Mantenimiento de perfil",
        btnAgregar,
        btnBuscar,
        loading: true,
        loadingSaveData: false,
        messagesNotification: [],
        errorDetected: false,
        activeNotification: false,
        dialogEdit: false,
        dialogEnableProfile: false,
        dialogDisableProfile: false,
        dialogAddProfile: false,
        editProfileSelected: 0,
        showAlertInfo: false,
        textShowAlertInfo: "",
        nameProfile: "",
        descriptionProfile: "",
        interactivities: [],
        selected: [],
        profiles: [],
        moduleItems: [],
        headers: [
          { text: 'ID', align: 'start', sortable: false, value: 'idProfile', },
          { text: 'Perfil', value: 'name' },
          { text: 'Estado', value: 'status' },
          { text: 'Opciones', value: 'numOptions' },
          { text: 'Usuarios', value: 'numUsers' },
          { text: 'Acciones', value: 'acciones', sortable: false }
        ],
        status : [
            {
                key: "true",
                name: "Activo"
            },
            {
                key: "false",
                name: "Inactivo"
            },
            {
                key: "all",
                name: "Todos"
            }
        ],
        statusSelected: {
            key: "all"
        },
    }),
    computed:{
        obtainItemDisabled: function (){
            try{
                return parseInt(localStorage.getItem("idProfile"));
            }catch(error){
                this.processError(["Error al intentar obtener el usuario de esta sesión."]);
                return 0;
            }
        }
    },
    methods:{
        processComplete(message){
            this.messagesNotification = message;
            this.errorDetected = false;
            this.activeNotification =  true;
        },
        processError(message){
            this.messagesNotification = message;
            this.errorDetected = true;
            this.activeNotification =  true;
        },
        async getProfiles(){
            this.loading = true;
            this.profiles = await findProfiles("all")
                .then((resp)=>{
                    if(resp.data.length > 0){
                        return resp.data;
                    }else{
                        return [];
                    }
                })
                .catch(error => {
                    this.processError(setterErrorData(error));
                    return [];
                });
                this.statusSelected.key = "all";
                this.loading = false;
        },
        async clickBuscar(){
            this.loading = true;
            this.profiles = await findProfiles(this.statusSelected.key)
                .then((resp)=>{
                    if(resp.data.length > 0){
                        return resp.data;
                    }else{
                        return [];
                    }
                })
                .catch(error => {
                    this.processError(setterErrorData(error));
                    return [];
                });
                this.loading = false;
        },
        async funcEditItem (idProfile) {
            this.editProfileSelected = idProfile;
             await findProfileOptions(idProfile)
            .then((resp)=>{
                if(resp.data.length > 0){
                    //Setear los valores
                    this.selected = setterProfilesInOptionsActives(resp.data);
                    this.moduleItems = resp.data;
                    this.dialogEdit = true;
                }else{
                    this.moduleItems = [];
                    this.dialogEdit = true;
                }
                
            })
            .catch(error => {
                this.dialogEdit = false;
                this.processError(setterErrorData(error));
                return [];
            });
            
        },
        async disableProfile(){
            await disableProfile(this.editProfileSelected)
            .then((resp)=>{
                this.processComplete(resp.messages);
                this.getProfiles();
            })
            .catch(error => {
                this.processError(setterErrorData(error));
            });
            this.closeDialog();
            this.getProfiles();
        },
        async enableProfile(){
            
            await enableProfile(this.editProfileSelected)
            .then((resp)=>{
                this.processComplete(resp.messages)
            })
            .catch(error => {
                this.processError(setterErrorData(error));
            });
            this.closeDialog();
            this.getProfiles();
        },
        async saveProfile () {
            try {
                const profileInOptionUpdates = createProfilesInOptionsUpdates(
                    this.selected, this.moduleItems, 
                    this.editProfileSelected, this.user=findLocalStorage('user'));
                this.loadingSaveData = true;
                await updateProfileInOption(profileInOptionUpdates)
                    .then((resp)=>{
                        this.closeDialog();
                        this.getProfiles();
                        this.processComplete(resp.messages)
                    })
                    .catch(error => {
                        this.textShowAlertInfo = "Ha ocurrido un error al guardar el perfil. Intente otra vez.: "+setterErrorData(error);
                        this.showAlertInfo = true;
                        this.selected = setterProfilesInOptionsActives(this.moduleItems);
                        this.loadingSaveData = false;
                    });
            } catch (error) {
                this.showAlertInfo = true;
                this.textShowAlertInfo = "Ha ocurrido un error al actualizar el perfil. Intente otra vez.";
                this.selected = setterProfilesInOptionsActives(this.moduleItems);
            }
        },
        async addProfile () {
            try {
                if((this.nameProfile.trim() === '') || (this.descriptionProfile.trim() === '') 
                || (this.descriptionProfile.trim().match(/\d+/g)?.length !== undefined) 
                || (this.nameProfile.trim().match(/\d+/g)?.length !== undefined) )
                    throw "Debe ingresar los datos correctamente. Recuerde no ingresar datos numéricos.";

                await saveProfile(this.nameProfile, this.descriptionProfile)
                .then((resp)=>{
                    this.processComplete(resp.messages)
                    this.closeDialog();
                    this.getProfiles();
                })
                .catch(error => {
                    this.processError(setterErrorData(error));
                    this.dialogAddProfile = true;
                });
            } catch (error) {
                this.showAlertInfo = error;
                this.dialogAddProfile = true;
            }
        },
        closeDialog(){
            this.dialogDisableProfile = false;
            this.dialogEnableProfile = false;
            this.dialogAddProfile = false;
            this.dialogEdit = false;
            this.showAlertInfo = false;
            this.loadingSaveData = false;
        },
        clickAgregar(){
            this.nameProfile = "";
            this.descriptionProfile = "";
            this.dialogAddProfile = true;
        },
        disableItem (idProfile) {
            this.editProfileSelected = idProfile;
            this.dialogDisableProfile = true;
        },
        enableItem(idProfile){
            this.editProfileSelected = idProfile;
            this.dialogEnableProfile = true;
        },
    },
    components:{
        Boton,
        TableEditAndDelete,
        ChargeData,
        CardProfile,
        DialogAcceptCancel,
        TextFieldSimpleData,
        Notification
    }
}
</script>